.homeContainer {
  margin-top: 140px;
  display: flex;
  justify-content: center;
  overflow: hidden;
}
.homeContainer img {
  animation: pulse 2s ease-in-out infinite;
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
  100% {
    transform: scale(1);
  }
}
